<template>
  <el-main>
    <div class="wrap-header">
      <div class="item"><span>数据来源：</span>zksync区块浏览器</div>
      <div class="item"><span>总地址数：</span>{{this.totalAccount}}</div>
      <div class="item"><span>24小时新增：</span>{{this.newAccount}}</div>
    </div>
    <el-table ref="multipleTable" :data="addrData" border show-summary :summary-method="getSummaries" tooltip-effect="dark" style="width: 100%" :key="tableKey" @selection-change="selectChange">
      <el-table-column type="selection" width="35"></el-table-column>
      <el-table-column type="index" label="#" :index="indexAdd" width="50"></el-table-column>
      <el-table-column prop="saddr" label="钱包地址" min-width="110"></el-table-column>
<!--      <el-table-column prop="remark" label="备注" width="90" show-overflow-tooltip></el-table-column>-->
      <el-table-column label="备注" width="80" show-overflow-tooltip>
        <template slot-scope="scope">
          <el-button icon="el-icon-edit" size="mini" circle @click="editOne(scope.$index, scope.row.remark)"></el-button>
          <span>{{ scope.row.remark }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="recent" label="最新" width="80" label-class-name="label-color" class-name="font-color2" sortable sort-by="recent">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.recent" placement="top">
            <span>{{ scope.row.srecent }}</span>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column prop="balance" label="eth" width="60"></el-table-column>
      <el-table-column label="zksync lite" label-class-name="bg-color1">
        <el-table-column prop="liteBridge" label="跨链U" width="80"></el-table-column>
        <el-table-column prop="liteTx" label="Tx" width="40"></el-table-column>
      </el-table-column>
      <el-table-column label="zksync era" label-class-name="bg-color2">
        <el-table-column prop="eraBridge" label="官桥" width="50"></el-table-column>
        <el-table-column prop="bridge" label="跨链U" width="80"></el-table-column>
        <el-table-column prop="eraTx" label="Tx" width="45"></el-table-column>
        <el-table-column prop="contract" label="合约数" width="65"></el-table-column>
        <el-table-column prop="month" label="活跃月" width="65"></el-table-column>
        <el-table-column prop="week" label="活跃周" width="65"></el-table-column>
        <el-table-column prop="day" label="活跃天" width="65"></el-table-column>
        <el-table-column prop="age" label="总时长" width="65"></el-table-column>
        <el-table-column prop="ethEra" label="ethEra" width="70"></el-table-column>
        <el-table-column prop="fee" label="gas" width="60"></el-table-column>
      </el-table-column>
      <el-table-column label="airdrop" label-class-name="bg-color4">
        <el-table-column prop="score" label="分" width="40"></el-table-column>
        <el-table-column prop="rank" label="排名" width="70"></el-table-column>
        <el-table-column prop="percent" label="前%" width="60"></el-table-column>
        <el-table-column prop="quality" label="品质" width="60">
          <template slot-scope="scope">
            <span class="quality-color1" v-if="scope.row.quality=='路人'">{{ scope.row.quality }}</span>
            <span class="quality-color2" v-if="scope.row.quality=='反撸'">{{ scope.row.quality }}</span>
            <span class="quality-color3" v-if="scope.row.quality=='保底'">{{ scope.row.quality }}</span>
            <span class="quality-color4" v-if="scope.row.quality=='精品'">{{ scope.row.quality }}</span>
          </template>
        </el-table-column>
      </el-table-column>
      <el-table-column width="50">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="deleteOne(scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 20px">
      <el-pagination background layout="prev, pager, next" :page-size="pageSize" :total="total" hide-on-single-page @current-change="handleChange"></el-pagination>
    </div>
    <div style="margin-top: 20px">
      <el-button type="primary" icon="el-icon-plus" @click="dialog1 = true">添加地址</el-button>
      <el-button type="primary" icon="el-icon-folder-add" @click="dialog2 = true">批量添加地址</el-button>
      <el-button type="danger" icon="el-icon-delete" @click="deleteAddr">删除选中地址</el-button>
      <el-button type="primary" icon="el-icon-download" @click="downloadData">下载</el-button>
      <el-divider direction="vertical"></el-divider>
      <el-button type="danger" @click="deleteAll">重置页面</el-button>
      <el-button type="success" @click="dialog3 = true">数据说明</el-button>
    </div>
    <el-dialog title="添加地址" :visible.sync="dialog1" width="30%">
      <div>
        <div style="margin-bottom:5px">地址</div><el-input v-model="addr"></el-input>
      </div>
      <div style="margin-top:10px">
        <div style="margin-bottom:5px">备注</div><el-input v-model="remark"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog1 = false">取 消</el-button>
        <el-button type="primary" @click="addAddr">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="批量添加地址" :visible.sync="dialog2" width="30%">
      <el-input type="textarea" :rows="10" placeholder="每行输入1个地址" v-model="addrText"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog2 = false">取 消</el-button>
        <el-button type="primary" @click="addMultiAddr">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="表格数据说明" :visible.sync="dialog3" width="30%">
      <p><strong>数据来源：</strong>zksync官方区块浏览器，实时数据。</p>
      <p><strong>空投分数：</strong>参照arb积分制排名，共20分，具体规则如下：</p>
      <ul>
        <li>精品号：排名<10w</li>
        <li>路人号：排名<30w</li>
        <li>保底号：排名<50w</li>
        <li>反撸号：排名>50w</li>
      </ul>
      <ul>
        <li>跨链到zkSync Lite</li>
        <li>在zkSync Lite上交互10次</li>
        <li>在zkSync Lite上交互25次</li>
        <li>跨链到zkSync Era</li>
        <li>使用过跨链桥跨过$10,000资产</li>
        <li>使用过跨链桥跨过$25,000资产</li>
        <li>使用过跨链桥跨过$50,000资产</li>
        <li>使用过跨链桥跨过$100,000资产</li>
        <li>在zkSync Era上交互10次</li>
        <li>在zkSync Era上交互25次</li>
        <li>在zkSync Era上交互100次</li>
        <li>与超过10个合约交互过</li>
        <li>与超过25个合约交互过</li>
        <li>与超过50个合约交互过</li>
        <li>在3个独立月使用过</li>
        <li>在6个独立月使用过</li>
        <li>在9个独立月使用过</li>
        <li>在10个独立周使用过</li>
        <li>在25个独立周使用过</li>
        <li>在50个独立周使用过</li>
      </ul>
    </el-dialog>
  </el-main>
</template>

<script>
import {getEthBalance as getBalance, getZkSyncTx, getZkLiteTx} from "../../../utils/getblock";
import {calcZksScore} from "../../../utils/calcscore";
import axios from "axios";

export default {
  name: "Zksync",
  data() {
    return {
      keyValue: 'zksAddr',
      tableKey: 0,
      addr: '',
      remark: '',
      addrText: '',
      addrData: [],
      addrSelected: [],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      total: 0,
      pageNum: 1,
      pageSize: 50,
      totalAccount: 2975372,
      newAccount: 0,
      okApi: "ad4621f2-8dbe-41fa-b60f-f7856cbe1390"
    }
  },
  mounted() {
    // 获取基本信息
    this.getBlockInfo();
    // 初始化addr
    let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
    if (addr != undefined) this.fetchData(addr);
  },
  methods: {
    // 获取基本信息
    getBlockInfo() {
      const config = {
        headers: {
          "Ok-Access-Key": this.okApi
        },
        params: {
          "chainShortName": "zksync"
        }
      };
      axios.get("https://www.oklink.com/api/v5/explorer/blockchain/address", config).then(res => {
        const ret = res.data.data[0];
        this.totalAccount = ret["totalAddresses"];
        this.newAccount = ret["newTotalAddresses24h"];
      })
    },
    // 添加地址
    addAddr() {
      this.dialog1 = false;
      let patter = /^0x[0-9a-fA-F]{40}$/;
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      if (addr == null) addr = [];
      let addr2 = addr.map((item, index, arr) => {
        return item.addr;
      });
      console.log(addr2);
      if (this.addr.match(patter) != undefined && addr2.indexOf(this.addr) == -1) {
        addr.push({'addr': this.addr, 'remark': this.remark});
        window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
        this.fetchData([{'addr': this.addr, 'remark': this.remark}]);
      } else {
        this.$message({
          message: '钱包地址格式不对或重复',
          type: 'warning'
        });
      }
    },
    // 添加多个地址
    addMultiAddr() {
      this.dialog2 = false;
      let arr = this.addrText.split("\n");
      let patter = /^0x[0-9a-fA-F]{40}$/;
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      let newAddr =[];
      if (addr == null) addr = [];
      let addr2 = addr.map((item, index, arr) => {
        return item.addr;
      });
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].match(patter) != undefined && addr2.indexOf(arr[i]) == -1) {
          newAddr.push({'addr': arr[i], 'remark': ''});
          addr.push({'addr': arr[i], 'remark': ''});
        } else {
          this.$message({
            message: '钱包地址格式不对或重复',
            type: 'warning'
          });
        }
      }
      window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
      this.fetchData(newAddr);
    },
    // 编辑一行
    editOne(index, text) {
      this.$prompt("备注", {
        confirmButtonText: "确定",
        inputValue: text
      }).then(({ value }) => {
        this.addrData[index]["remark"] = value;
        this.tableKey = Math.random();
        let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
        let index2 = index + (this.pageNum - 1) * this.pageSize;
        addr[index2]["remark"] = value;
        window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
      });
    },
    // 删除一行
    deleteOne(index) {
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      this.addrData.splice(index, 1);
      addr.splice(index, 1);
      window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
    },
    // 删除地址
    deleteAddr() {
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      for (let i = 0; i < addr.length; i++) {
        if(this.addrSelected.indexOf(addr[i].addr) != -1) {
          this.addrData.splice(i, 1);
          addr.splice(i, 1);
          i--;
        }
      }
      window.localStorage.setItem(this.keyValue, JSON.stringify(addr));
    },
    // 全部删除
    deleteAll() {
      this.addrData = [];
      window.localStorage.setItem(this.keyValue, JSON.stringify([]));
    },
    // 下载数据
    downloadData(arr) {

    },
    // 选择变化
    selectChange(val) {
      this.addrSelected = [];
      for (let i = 0; i < val.length; i++) {
        this.addrSelected.push(val[i].addr);
      }
    },
    // index索引
    indexAdd(index) {
      return index + 1 + (this.pageNum - 1) * this.pageSize;
    },
    // 页面变化
    handleChange(val) {
      let addr = JSON.parse(window.localStorage.getItem(this.keyValue));
      let min = (val - 1) * this.pageSize;
      this.pageNum = val;
      addr = addr.slice(min, min + this.pageSize);
      this.addrData = [];
      this.addrSelected = [];
      this.fetchData(addr);
    },
    // 获取数据
    fetchData(arr) {
      let len = this.addrData.length;
      for (let i = 0; i < arr.length; i++) {
        let addr = arr[i].addr;
        this.addrData[len+i] = [];
        this.addrData[len+i]['addr'] = addr;
        this.addrData[len+i]['saddr'] = addr.substring(0,6) + "..." + addr.substring(addr.length-4, addr.length);
        this.addrData[len+i]['remark'] = arr[i].remark;
        getBalance("ethereum", addr).then(res => {
          this.addrData[len+i]['balance'] = res;
          this.tableKey = Math.random();
        });
        getBalance("zksyncera", addr).then(res => {
          this.addrData[len+i]['ethEra'] = res;
          this.tableKey = Math.random();
        });
        // era: recent, contracts, months, weeks, days, age, fee
        getZkSyncTx(addr).then(res => {
          this.addrData[len+i]['recent'] = res[0];
          this.addrData[len+i]['srecent'] = res[1];
          this.addrData[len+i]['contract'] = res[2];
          this.addrData[len+i]['month'] = res[3];
          this.addrData[len+i]['week'] = res[4];
          this.addrData[len+i]['day'] = res[5];
          this.addrData[len+i]['age'] = res[6];
          this.addrData[len+i]['fee'] = res[7].toFixed(4);
          this.addrData[len+i]['eraTx'] = res[8];
          this.addrData[len+i]['eraBridge'] = res[9];
          this.addrData[len+i]['bridge'] = '$' + res[10];
          if (this.addrData[len+i]['liteTx'] != undefined) {
            let ret = calcZksScore(this.addrData[len+i], this.totalAccount);
            this.addrData[len+i]['score'] = ret[0];
            this.addrData[len+i]['rank'] = ret[1];
            this.addrData[len+i]['percent'] = ret[2] + "%";
            this.addrData[len+i]['quality'] = ret[3];
          }
          this.tableKey = Math.random();
        });
        // lite: liteBridge, liteTx
        getZkLiteTx(addr).then(res => {
          this.addrData[len+i]['liteTx'] = res[0];
          this.addrData[len+i]['liteBridge'] = '$' + res[1];
          if (this.addrData[len+i]['eraTx'] != undefined) {
            let ret = calcZksScore(this.addrData[len+i], this.totalAccount);
            this.addrData[len+i]['score'] = ret[0];
            this.addrData[len+i]['rank'] = ret[1];
            this.addrData[len+i]['percent'] = ret[2] + "%";
            this.addrData[len+i]['quality'] = ret[3];
          }
          this.tableKey = Math.random();
        });
      }
    },
    // 表尾
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 2) {
          sums[index] = '合计';
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value)) && ([5, 16, 17]).indexOf(index) >= 0) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0).toFixed(4);
        } else {
          sums[index] = '';
        }
      });
      return sums;
    }
  },
}
</script>

<style scoped lang="stylus">
$color1 = #3651f1
$color2 = #d44ec2
$color3 = #d28905
$color4 = #07ab22
$textColor = #606266
$greyColor = #e6e6e6

.wrap-header
  display flex
  padding-bottom 10px
  margin-bottom 20px
  font-size 14px
  border-bottom solid 1px $greyColor
  .item
    margin-right 50px
    color $color4
    span
      color $textColor

/deep/.el-table
  .cell
    text-align center
    padding 0 5px
  .el-table__cell
    padding 8px 0
  .bg-color1
    color #fff
    background-color $color1 !important
  .bg-color2
    color #fff
    background-color $color2 !important
  .bg-color3
    color #fff
    background-color $color3 !important
  .bg-color4
    color #fff
    background-color $color4 !important
  .font-color2
    color $color2
    font-weight bold
  .label-color
    color #909399

// quality color
.quality-color1
  color #fff
  background-color $color1
  padding 3px 8px
  border-radius 4px
  font-weight bold
.quality-color2
  color #fff
  background-color $color2
  padding 3px 8px
  border-radius 4px
  font-weight bold
.quality-color3
  color #fff
  background-color $color3
  padding 3px 8px
  border-radius 4px
  font-weight bold
.quality-color4
  color #fff
  background-color $color4
  padding 3px 8px
  border-radius 4px
  font-weight bold

</style>



